<template>
    <el-button 
        v-if="isComponents"
        type="primary"
        size="small"
        class="my-button" 
        :style="dataOptions.style"
    >
    {{dataOptions.text}}
    </el-button>
    <!-- 样式修改 -->
    <div v-else class="attribute-area">
        <template v-if="!lodash.isEmpty(options)">
        <!-- text文字  :value="dataOptions.text" @input="change($event)"-->
            <div v-if="(!!('text' in options))" class="line-item">
                <label class="label">文   字：</label>
                <input class="value" v-model="dataOptions.text" placeholder="请输入" type="text" />
            </div>
        <!-- style -->
            <div v-if="dataOptions.style">
                <!-- <em class="line-title">样式</em> -->
                <div v-if="!!('backgroundColor' in options.style)" class="line-item">
                    <label class="label">背景颜色：</label>
                    <input class="color" v-model="dataOptions.style.backgroundColor" type="color">
                </div>
                <div v-if="!!('color' in options.style)" class="line-item">
                    <label class="label">字体颜色：</label>
                    <input class="color" v-model="dataOptions.style.color" type="color">
                </div>
                <div v-if="!!('fontSize' in options.style)" class="line-item">
                    <label class="label">字体大小：</label>
                    <input class="value" v-model="dataOptions.style.fontSize" placeholder="请输入" type="text">
                </div>
                <div v-if="!!('width' in options.style)" class="line-item">
                    <label class="label">按钮尺寸：</label>
                    <div class="line-right">
                        <div 
                            :class="['right-btn', dataOptions.style.width == item.style.width ? 'btn-active' : '']" 
                            :style="item.style" 
                            v-for="(item,index) in sizeArr"
                            :key="index"
                            @click="activeTap(item)"
                        >
                            {{item.text}}
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </div>
   
</template>
<script>
import lodash from 'lodash'
export default {
  data() {
    return {
        lodash,
        dataOptions: {},
        sizeArr: [
            { text: 'medium', style: {width: '112px', height: '37px'} },
            { text: 'small', style: {width: '90px', height: '35px'} },
            { text: 'mini', style: {width: '60px', height: '30px'} },  
        ]
    }
  },
  props:{
    select: {
        value: Boolean,
		default:() => false
    },
    isComponents: {
        value: Boolean,
		default:() => true
    },
    options:{
        value: Object,
		default:() => {}
    }
  },
  computed:{
    // dataOptions(){
    //   console.log(this.options)
    //   return {...this.options}
    // }
  },
  created() {
    // const temp = lodash.cloneDeep(this.options)
    this.dataOptions = this.options
  },
  updated() {
    const temp = this.options
    this.dataOptions = temp
  },
  methods: {
    activeTap(item) {
        this.dataOptions.style.width = item.style.width
         this.dataOptions.style.height = item.style.height
    },
    //  change(e) {
    //     console.log(e.currentTarget.value);
    //     const temp = this.dataOptions
    //     temp.text = e.currentTarget.value
    //     this.dataOptions = temp
    //     this.$forceUpdate()
    //     console.log(this.dataOptions);
    // },
  }
}
</script>
<style scoped>
    .my-button {
        border: none;
        /* cursor: pointer;
        border: none;
        width: 75px;
        height: 35px;
        line-height: 35px;
        font-size: 15px;
        font-weight: 500;
        color: #fff;
        display: inline-block;
        text-align: center;
        background-color: #409eff;
        border-radius: 5px; */
    }

    .line-item {
        width: 100%;
        padding: 10px 10px 5px 10px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
    .line-item .label {
        width: 80px;
        text-align: left;
    }
    .line-item .value {
        flex: 1;
        height: 35px;
        min-width: 40px;
        padding: 0 5px;
        color: #888;
        border: 1px solid #ddd;
    }
    .line-item .color {
        width: 40px;
        height: 35px;
        padding: 0 5px;
        border: 1px solid #ddd;
    }

    .line-right {
        flex: 1;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .line-right .right-btn {
        width: 100px;
        height: 35px;
        border: 1px solid #000;
        border-radius: 2px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .line-right .btn-active {
        color: #fff;
        background-color: #000;
    }


</style>
