<template>
    <el-select v-if="isComponents" v-model="dataOptions.val" placeholder="请选择">
        <el-option
            v-for="item in dataOptions.options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
    </el-select>
</template>
<script>
export default {
  data() {
    return {
    }
  },
  props:{
    options:{},
    isComponents: {
        value: Boolean,
		    default:() => true
    },
  },
  computed:{
    dataOptions(){
      console.log(this.options)
      return {...this.options}
    }
  }
}
</script>