<template>
  <div class="container">
    <div class="slide-view">
      <div class="slide-child" v-for="(item,index) in componentList" :key="index">
        <div class="item-head">
          <span>{{item.title}}</span>
          <img @click="arrowTap(index)" :class="['pointer',item.open ? '' : 'transform180']" src="@/assets/icon/arrow-up.png" alt="">
        </div>
        <draggable
          :class="['slide',item.open ? '' : 'item-hidden']"
          v-model="componentList[index].list"
          v-bind="dragOptions"
          :options="{
            sort: false,
            group: { name: 'field', pull: 'clone', put: false },
          }"
        >
          <div
            class="slide-name" 
            v-for="(it, ind) in item.list"
            :key="ind"
          >
            {{ it.name }}
          </div>
        </draggable>
      </div>
    </div>
    <div class="content">
      <div class="show-view">
        <p class="show-title">展示区</p>
        <draggable
          group="field"
          v-bind="dragOptions"
          :list="selectList"
          class="show-area"
          @change="changeHandle"
        >
          <div 
            :class="['show-component',index === selectInd ? 'show-active' : '']" 
            v-for="(item, index) in selectList"
            :key="index"
            @click.stop="itemSelect(item,index)"
          >
            {{ item.hiddenLabel ? '': item.name }}
            <room :contentView="item.componentName" :isComponents="true" :options="item.options"></room>
          </div>
        </draggable>
      </div>
      <div class="code-view">
        <div class="code-head">
          <div class="code-left"></div>
          <div class="code-title">Code</div>
          <div class="code-right">
            <el-button v-if="selectList.length > 0" class="copy" @click="codeCopy" type="primary" size="small"> Copy All </el-button>
          </div>
        </div>
        <pre class="code-pre" v-if="selectList.length > 0">
          <code id="codeBox">
            &lt;template&gt;
              &lt;div&gt;
                &lt;div class="name" v-for="(item, index) in list" :key="index"&gt;
                  &lt;room :contentView="item.componentName" :options="item.options"&gt;&lt;/room&gt;
                &lt;/div&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            import room from '@/components/room.vue'
            export default {
                data() {
                  return {
                      list: [
                      <template v-for="(item) in selectList">{{item}},</template>
                      ]
                  }
                },
                components: {
                  room
                },
            }
          </code>
        </pre>
        
      </div>
    </div>
    <div class="attribute-view">
      <p class="attribute-title">属性</p>
      <template v-if="!lodash.isEmpty(selectCom)">
          <attr-com :contentView="selectCom.componentName" :options="selectCom.options"></attr-com>
      </template>
    </div>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
import room from '@/components/room.vue'
import attrCom from '@/components/attr.vue'
import lodash from 'lodash'
// import header from '@/components/header.vue'
export default {
  data() {
    return {
      lodash,
      dragOptions: {
        animation: 500,
      },
      componentList: [
        {
          open: true,
          title: '基础组件',
          list: [
            { 
              name: '按钮', 
              componentName: 'mybutton',
              hiddenLabel: true, 
              options:{
                text:'按钮',
                style: {
                  backgroundColor: '#000000',
                  borderColor: '#000000',
                  fontSize: '14px',
                  color: '#ffffff',
                  borderRadius: '2px',
                  width: '112px',
                  height: '37px',
                }
              } 
            },
            { name: '单选', componentName: 'radio', options:{val:'1'} },
            // { name: '多选', componentName: 'checkbox', options:{val:'0'}},
            { name: '输入框', componentName: 'myinput', options:{val:'1'}},
            { name: '下拉框', componentName: 'selectCom', options:{val:1, options:[{value: 1, label: '黑色'}, {value: 2,label: '渐变色'}]}},
            { name: '开关', componentName: 'switchCom', options:{val:true}},
          ]
        },
        {
          open: false,
          title: '导航组件',
          list: []
        },
        {
          open: false,
          title: '其他组件',
          list: [
            { name: '表格', componentName: 'mytable', options:{}},
          ]
        },
      ],
      selectList: [],
      selectInd: -1,
      selectCom: null,
    }
  },
  components: {
    draggable,
    room,
    attrCom,
  },
  created() {
    console.log(room)
  },
  mounted () {
   //防止火狐浏览器拖拽的时候以新标签打开
    document.body.ondrop = function (event) {
      event.preventDefault();
      event.stopPropagation();
    }
  },
  methods: {

    changeHandle() {
      console.log(this.selectList);
    },

    itemSelect(item,index) {
      this.selectInd = index;
      this.selectCom = item;
    },

    arrowTap(index) {
      const temp = this.componentList;
      temp[index].open = !temp[index].open;
      this.componentList = temp;
    },

    codeCopy() {
      var val = document.getElementById('codeBox');
      window.getSelection().selectAllChildren(val);
      document.execCommand("Copy");
      this.$message.success('已复制');
    },
  },
}
</script>
<style src="./index.css" scoped></style>
