<template>
    <el-input v-if="isComponents" placeholder="输入框" v-model="dataOptions.val"></el-input>
</template>
<script>
import lodash from 'lodash'
export default {
  data() {
    return {
        lodash,
        dataOptions: {}
    }
  },
  props:{
    isComponents: {
        value: Boolean,
		    default:() => true
    },
    dataOptions(){
      console.log(this.options)
      return {...this.options}
    }
  },
}
</script>
